import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { AdminUserModel } from '../../models/AdminUserModel';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { MatchValidator } from '../../utils/helpers/match.validator';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from '../../services/auth.service';
import { ResponseData } from '../../models/ResponseData';
import { ResponseCode } from 'src/app/utils/enums/responseCode.enum';

@Component({
    selector: 'app-user-edit',
    templateUrl: 'user-edit.component.html',
    providers: [UserService],

})

export class UserEditComponent implements OnInit {
    userModel: AdminUserModel;
    registerForm: FormGroup;
    submitted = false;
    isAdminRole: boolean;
    editMode: string;
    selectedGroupRoles: [];
    //groupRoles: AdminGroupModel[];

    constructor(
        private activatedRoute: ActivatedRoute,
        private router: Router,
        private formBuilder: FormBuilder,
        private userService: UserService,
        private toastr: ToastrService
    ) {
        //this.userModel = new AdminUserModel();
    }

    ngOnInit(): void {
        this.isAdminRole = AuthService.IsAdminRole;
        const userId = parseInt(this.activatedRoute.snapshot.queryParams['userId']);
        //var userModel = null;
        if (userId > 0) {
            this.editMode = "Update";
            // Khi to form vi trng hp edit
            this.registerForm = this.formBuilder.group({
                userId: [0],
                username: [{ value: '', disabled: true }, Validators.required],
                password: ['', [Validators.minLength(6), Validators.maxLength(24)]],
                confirmPassword: [''],
                fullname: [''],
                email: ['', [Validators.required]],
                phone: ['', [Validators.required]],
                description: [''],
                isAdminGroup: [false],
                groups: '',
                status: [0, Validators.required]
            }, {
                validator: MatchValidator('password', 'confirmPassword')
            });
            this.userService.getToEdit(userId).subscribe(response => {
                if (response.success) {
                    this.loadForm(response.data);
                } else {
                    this.toastr.error("UserId khng hp l");
                    this.router.navigate(['/user/list']);
                }
            }, error => { this.toastr.error(error); this.router.navigate(['/user/list']); });
        }
        else {
            this.editMode = "Add";
            // Khi to form vi trng hp Add
            this.registerForm = this.formBuilder.group({
                userId: [0],
                username: ['', Validators.required],
                password: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(24)]],
                confirmPassword: ['', Validators.required],
                fullname: [''],
                email: ['', [Validators.required]],
                phone: ['', [Validators.required]],
                description: [''],
                isAdminGroup: [false],
                groups: '',
                status: [0, Validators.required]
            }, {
                validator: MatchValidator('password', 'confirmPassword')
            });
        }
        this.userService.getGroupRoles().subscribe(response => {
            const res = response as ResponseData;
            if (res && res.code == ResponseCode.Success) {
                // this.groupRoles = res.data as AdminGroupModel[];
            } else {
                this.toastr.error("UserId khng hp l");
                this.router.navigate(['/user/list']);
            }
        }, error => { this.toastr.error(error); this.router.navigate(['/user/list']); });
    }
    loadForm(userModel: AdminUserModel) {
        this.registerForm.patchValue({
            userId: userModel.userId,
            username: userModel.username,
            //password: userModel.password,
            //confirmPassword: userModel.confirmPassword,
            fullname: userModel.fullname,
            email: userModel.email,
            phone: userModel.phone,
            description: userModel.description,
            isAdminGroup: userModel.isAdminGroup,
            groups: userModel.groups === '' ? [] : userModel.groups.split(',').map(Number),
            status: userModel.status
        });
    }

    // convenience getter for easy access to form fields
    get f() { return this.registerForm.controls; }

    onSubmit() {
        this.submitted = true;
        // stop here if form is invalid
        if (this.registerForm.invalid) {
            return;
        }

        this.userService.update(this.registerForm.getRawValue()).subscribe(response => {
            if (response.success) {
                if (this.registerForm.value.userId === 0) {
                    this.toastr.success("Bn  thm ti khon qun tr thnh cng.");
                }
                else {
                    this.toastr.success("Bn  sa ti khon qun tr thnh cng.");
                }
                this.router.navigate(['/user/list']);
            } else {
                this.toastr.error(response);
            }
        }, error => { this.toastr.error(error); });
    }

    onReset() {
        this.submitted = false;
        this.registerForm.reset();
    }
}
